#game-root{height:calc(100% - 4rem)}

#start-root{height:100%;display:flex;flex:1 1;flex-direction:column;justify-content:center;text-align:center;padding-bottom:4rem}#start-select-div{font-size:1.2rem;margin-bottom:0.6rem}#start-select-div>label{display:block;margin-bottom:0.6rem;font-weight:bold}#start-select-div>select{width:8rem;height:3rem;outline:none;border:none;border-radius:4px;background:#495057;color:white;text-align:center;font-size:1rem;font-weight:bold}#start-btn-div>button{float:none;width:8rem;height:3rem;font-size:1.2rem}

.block{float:left;outline:none;margin:0;padding:0;text-align:center;font-weight:bold;font-size:2rem;width:2rem;height:2rem;line-height:2rem;border:none;cursor:pointer}@media (max-width: 720px){.block.normal{font-size:1.8rem;width:1.8rem;height:1.8rem;line-height:1.8rem}.block.normal>img{width:1.8rem;height:1.8rem}}@media (max-width: 550px){.block.normal{font-size:1.2rem;width:1.2rem;height:1.2rem;line-height:1.2rem}.block.normal>img{width:1.2rem;height:1.2rem}}@media all and (max-width: 1440px){.block.hard{font-size:1.8rem;width:1.8rem;height:1.8rem;line-height:1.8rem}.block.hard>img{width:1.8rem;height:1.8rem}}@media all and (max-width: 1024px){.block.hard{font-size:1.6rem;width:1.6rem;height:1.6rem;line-height:1.6rem}.block.hard>img{width:1.6rem;height:1.6rem}}@media all and (max-width: 650px){.block.hard{font-size:1.2rem;width:1.2rem;height:1.2rem;line-height:1.2rem}.block.hard>img{width:1.2rem;height:1.2rem}}@media all and (max-width: 450px){.block.hard{font-size:0.8rem;width:0.8rem;height:0.8rem;line-height:0.8rem}.block.hard>img{width:0.8rem;height:0.8rem}}.block.open-block{background:#e9ecef}.block.open-block.val-1{color:#1976d2}.block.open-block.val-2{color:#388e3c}.block.open-block.val-3{color:#d32f2f}.block.open-block.val-4{color:#7b1fa1}.block.open-block.val-5{color:#ffcd1a}.block.closed-block{background:#495057;color:#be3333}.block.closed-block:hover{background:#868e96}.block>img{width:2rem;height:2rem}.block.highlight{background:#868e96}

#board-root{display:flex;flex-direction:column;align-items:center;height:100%}#board-minNum-div{display:inline-block;width:100%;border-bottom:1px solid #e9ecef;margin-bottom:3rem}#board-minNum-div>h2{height:2rem;text-align:center}@media (max-width: 550px){#board-minNum-div>h2{height:1.2rem;font-size:1.2rem}}.board{display:inline-block;width:calc(10 * 2rem)}.board.normal{width:calc(18 * 2rem)}@media (max-width: 720px){.board.normal{width:calc(18 * 1.8rem)}}@media (max-width: 550px){.board.normal{width:calc(18 * 1.2rem)}}.board.hard{width:calc(24 * 2rem)}@media (max-width: 1440px){.board.hard{width:calc(24 * 1.8rem)}}@media (max-width: 1024px){.board.hard{width:calc(24 * 1.6rem)}}@media (max-width: 650px){.board.hard{width:calc(24 * 1.2rem)}}@media (max-width: 450px){.board.hard{width:calc(24 * 0.8rem)}}.board-restart{height:7rem;margin-top:1rem}.board-restart>button{width:100%;height:3rem;font-size:1.2rem;color:#efefef}

html{height:100%;font-size:15px}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box;height:100%}a{color:inherit;text-decoration:none}*{box-sizing:inherit}#root{height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

